.Calendar {
  width: max-content;
  height: max-content;
  margin: 0 auto;

  border-radius: 5px;

  &__list {
    cursor: pointer;
    font-size: 12px;
    background-color: rgb(200, 148, 43);

    :hover {
      background-color: grey;
      scale: 1.05;
    }

    &__text {
      display: flex;
      justify-content: space-between;

      color: rgb(96, 98, 126);

      border-bottom: 1px solid #000;
    }

    &--done {
      background-color: rgb(100, 162, 89);
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    height: 100px;
    overflow-y: auto;
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  &__day {
    width: 160px;

    height: 110px;
    margin-top: 2px;

    border: 1px solid black;
    border-radius: 5px;

    &--toDay {
      background-color: rgb(142, 188, 196);
    }

    &--none {
      opacity: 0;
    }
  }

  &__title {
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    background-color: white;

    font-size: 12px;

    border-bottom: 1px solid #000;
    z-index: 1;

    &--active {
      background-color: rgb(142, 188, 196);
    }
  }

  &__row {
    display: flex;
    gap: 2px;

    :hover {
      scale: 1.005;
    }
  }

  &__text {
    color: blue;

    &--red {
      color: red;
    }
  }
}

.elem::-webkit-scrollbar {
  width: 0;
  height: 0;
}